.product-list {
  /* background-color: #d8e7fe; */
  border: 1px solid rgb(226, 223, 223);
  width: 100%;
  padding: 15px;
}

.wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 20px;
  border: 1px solid rgb(226, 223, 223);
  /* border: 2px solid; */
  padding: 12px;
}

.product-text {
  text-decoration: none;
  color: black;
}

.product-price-btn {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.product-price-btn p {
  font-weight: bold;
  font-size: 25px;
}

.add-to-cart-btn {
  color: var(--purple);
  padding: 8px;
  border-radius: 5px;
}

@media (min-width: 320px) and (max-width: 500px) {
  .product-list {
    padding: 0;
    border: none;
  }
  .wrapper {
    flex-direction: column-reverse;
    width: 90%;
    margin: 8px;
    padding: 10px;
  }

  .product-img img {
    width: 100%;
    height: 95%;
    /* height: 125px; */
  }
}
